module.exports = "<p>Aujourd'hui est le premier jour de mon temps partiel. Ca ne fait que 2 ans que je suis dans la vie active et pourtant j'ai fait le choix de travailler moins.</p>\n<h2 id=\"mais-pourquoi\"><a class=\"header-anchor\" href=\"#mais-pourquoi\" aria-hidden=\"true\">#</a> Mais pourquoi ?!</h2>\n<p>Tout content, je l'ai annoncé à mes proches, à mes amis, etc. Et de manière assez unanime tous ceux qui ne gravitent pas autour du web m'ont regardé avec un air surpris, voire inquiet.</p>\n<blockquote>\n<p>– Tu as des problèmes de santé ? <em>Non.</em><br>\n– Ta boîte va mal ? <em>Au contraire.</em><br>\n– Ils ne sont pas content de toi ? <em>Pas que je sache.</em><br>\n– Tu ne t'y plais pas ? <em>Ah si ! Pas envie de partir.</em><br>\n– Ah ! Vous attendez un enfant ! <em>Euh, non. Pas encore :)</em></p>\n</blockquote>\n<p>En fait, c'est justement parce que tout va bien physiquement et financièrement que j'ai la chance de pouvoir faire ce choix.</p>\n<p>C'est donc uniquement pour des raisons positives. J'ai envie de passer du temps sur mes projets personnels qui me font grandir techniquement et humainement. Je veux aussi retrouver le temps de faire autre chose que du code comme je pouvais le faire pendant mes études. Et j'ai surtout soif de tout pouvoir partager. Pour avoir donné des formations, je sais que c'est quelque chose qui me tient de plus en plus à coeur.</p>\n<p>Avant aujourd'hui il fallait que je prenne ce temps sur les soirs et week-ends, entrant en compétition avec le temps passé avec mes proches. Je n'ai pas envie de faire de choix alors je vais faire les deux.</p>\n<p>Pour éviter que ce vendredi ne se transforme en début de week-end, je compte m'imposer plein de petites règles que j'ajusterai dans le temps (réveil à la même heure, planning défini pour la journée, etc.). Ce sera donc un jour travaillé comme les autres à ceci près que je ne gagnerai pas de sous.</p>\n<h2 id=\"on-commence-par-quoi-du-coup\"><a class=\"header-anchor\" href=\"#on-commence-par-quoi-du-coup\" aria-hidden=\"true\">#</a> On commence par quoi du coup ?</h2>\n<p>Je vais commencer par là où je me sens le plus à l'aise pour le moment pour ne pas freiner cet élan de motivation. La priorité est donc sur deux points :</p>\n<ul>\n<li>\n<p><strong>Affiner <a href=\"https://github.com/JulienPradet/pigment-store\">Pigment-Store</a> pour le rendre utilisable en production</strong><br>\nC'est un projet que j'ai commencé il y a maintenant un petit moment qui permet de générer une documentation pour tous les éléments visuels d'une application en React.<br>\nL'idée est de faire un pont entre les styleguides des graphistes et les tests unitaires des développeurs.<br>\nPour ceux qui connaissent un petit peu c'est à mis chemin entre <a href=\"https://github.com/storybooks/react-storybook\">React-Storybook</a> et <a href=\"http://patternlab.io/\">PatternLab</a>.</p>\n</li>\n<li>\n<p><strong>Rédiger et *publier* des articles</strong><br>\nJ'ai déjà quelques articles qui trainent depuis plusieurs mois, mais n'ai jamais pris le temps de les publier. C'est l'occasion !<br>\nJe partagerai sur ma façon de travailler sur React pour commencer. Mais le contenu se construira au fil de mes explorations.</p>\n</li>\n</ul>\n<p>Qui sait où tout ça va me mener ! Vers plein de choses nouvelles j'espère :)</p>\n<p>A bientôt !</p>\n"

